.jonnitto-googlemaps-map {
  margin: 0 auto 1.5rem;
}
.neos-backend .jonnitto-googlemaps-map {
  min-height: 250px;
}
@media (min-width: 768px) {
  .neos-backend .jonnitto-googlemaps-map {
    min-height: 350px;
  }
}
@media (min-width: 992px) {
  .neos-backend .jonnitto-googlemaps-map {
    min-height: 450px;
  }
}
@media (min-width: 1200px) {
  .neos-backend .jonnitto-googlemaps-map {
    min-height: 550px;
  }
}

.jonnitto-googlemaps-streetview,
.jonnitto-googlemaps-mapview {
  visibility: hidden;
  width: 100%;
  height: 250px;
}
.jonnitto-googlemaps-streetview.jonnitto-googlemaps-init,
.jonnitto-googlemaps-mapview.jonnitto-googlemaps-init {
  visibility: visible;
}
@media (min-width: 768px) {
  .jonnitto-googlemaps-streetview,
.jonnitto-googlemaps-mapview {
    height: 350px;
  }
}
@media (min-width: 992px) {
  .jonnitto-googlemaps-streetview,
.jonnitto-googlemaps-mapview {
    height: 450px;
  }
}
@media (min-width: 1200px) {
  .jonnitto-googlemaps-streetview,
.jonnitto-googlemaps-mapview {
    height: 550px;
  }
}

.jonnitto-googlemaps-mapview {
  color: #222;
}
.jonnitto-googlemaps-mapview img {
  max-width: none;
}

@media (min-width: 992px) {
  .jonnitto-googlemaps-both-wrapper::after {
    content: "";
    display: table;
    clear: both;
  }

  .jonnitto-googlemaps-both-mapview,
.jonnitto-googlemaps-both-streetview {
    position: relative;
    min-height: 1px;
    float: left;
    width: 50%;
  }
}

